/* istanbul ignore file */
import {
  CangjieClipboardEvent,
  constants,
  transferUtils,
  Controller,
} from '@ali/4ever-cangjie';
import { unwrapRefBlock } from '../utils/ccp';

const { MIME_TYPES } = constants;

export default function onCangjieCopyOrCut(
  event: CangjieClipboardEvent,
  controller: Controller,
  next,
) {
  if (!event.clipboardData) {
    return next();
  }

  const rawFragment = event.clipboardData.getData(MIME_TYPES.FRAGMENT);

  if (!rawFragment) {
    return next();
  }

  const fragment = transferUtils.decodeFragment(rawFragment);
  const newFragment = unwrapRefBlock(fragment);

  // 没有对 fragment 产生变更，则意味着不含有引用块
  if (newFragment === fragment) {
    return next();
  }

  return next(
    event.setClipboardData(
      event.clipboardData.setData(
        MIME_TYPES.FRAGMENT,
        transferUtils.encodeFragment(newFragment),
      ),
    ),
  );
}
